import React from "react";
import {
  Container,
  Typography,
  Box,
  Card,
  CardContent,
  CardActions,
  Button,
  CardMedia,
} from "@mui/material";
import classroomImage from "../assets/images/IMG_8505.jpg";
import studentsImage from "../assets/images/IMG_8510.jpg";
import teacherImage from "../assets/images/IMG_8507.jpg";

const courses = [
  {
    title: "Uluslararas Snav Hazrlk",
    description:
      "TOEFL, IELTS, PTE gibi uluslararas snavlara ynelik zel eitim programlar.",
    image: teacherImage,
    duration: "3-6 Ay",
    level: "B2 ve zeri",
  },
  {
    title: "SYM Snav Hazrlk",
    description:
      "YDS, YKDL, YKS-DL gibi SYM snavlarna ynelik uzman eitim.",
    image: studentsImage,
    duration: "4-8 Ay",
    level: "B1 ve zeri",
  },
  {
    title: "Kids & Teens Eitimi",
    description:
      "ocuklar ve genler iin zel tasarlanm eitim programlar.",
    image: classroomImage,
    duration: "8-12 Ay",
    level: "Tm Seviyeler",
  },
  {
    title: "Profesyonel Meslek Gruplar",
    description:
      "Avukat, doktor, mhendis gibi profesyoneller iin zel programlar.",
    image: teacherImage,
    duration: "6-12 Ay",
    level: "B2 ve zeri",
  },
  {
    title: "TESOL Sertifika Program",
    description: "Uluslararas ngilizce retmenlii sertifikas program.",
    image: studentsImage,
    duration: "4 Ay",
    level: "C1 ve zeri",
  },
  {
    title: "Dier Diller",
    description: "Almanca, Rusa, Franszca ve spanyolca eitimleri.",
    image: classroomImage,
    duration: "6-12 Ay",
    level: "Tm Seviyeler",
  },
];

const Courses = () => {
  return (
    <Container maxWidth="lg" sx={{ py: 8 }}>
      <Box sx={{ mb: 8, textAlign: "center" }}>
        <Box
          component="img"
          src="/heart_of_english_logo.svg"
          alt="Heart of English Logo"
          sx={{
            height: { xs: "80px", md: "120px" },
            mb: 4,
            filter: "drop-shadow(0px 4px 8px rgba(0,0,0,0.1))",
          }}
        />
        <Typography
          variant="h3"
          component="h1"
          gutterBottom
          sx={{ fontWeight: "bold" }}
        >
          Eitim Programlarmz
        </Typography>
        <Typography
          variant="h6"
          color="text.secondary"
          sx={{ maxWidth: 800, mx: "auto" }}
        >
          VIP konseptli dil eitimi ve yurtd eitim danmanl
          hizmetlerimizle yannzdayz.
        </Typography>
      </Box>

      <Box
        sx={{
          display: "flex",
          flexWrap: "wrap",
          gap: 4,
          justifyContent: "center",
        }}
      >
        {courses.map((course, index) => (
          <Box
            key={index}
            sx={{
              width: {
                xs: "100%",
                sm: "calc(50% - 16px)",
                md: "calc(33.33% - 16px)",
              },
            }}
          >
            <Card
              sx={{
                height: "100%",
                display: "flex",
                flexDirection: "column",
                transition: "transform 0.3s ease, box-shadow 0.3s ease",
                "&:hover": {
                  transform: "translateY(-8px)",
                  boxShadow: 6,
                },
              }}
            >
              <CardMedia
                component="img"
                height="200"
                image={course.image}
                alt={course.title}
                sx={{ objectFit: "cover" }}
              />
              <CardContent sx={{ flexGrow: 1 }}>
                <Typography
                  gutterBottom
                  variant="h5"
                  component="div"
                  sx={{ fontWeight: "bold" }}
                >
                  {course.title}
                </Typography>
                <Typography
                  variant="body2"
                  color="text.secondary"
                  sx={{ mb: 2 }}
                >
                  {course.description}
                </Typography>
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "space-between",
                    mb: 2,
                  }}
                >
                  <Typography variant="body2" color="text.secondary">
                    Sre: {course.duration}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    Seviye: {course.level}
                  </Typography>
                </Box>
              </CardContent>
              <CardActions>
                <Button
                  variant="contained"
                  fullWidth
                  sx={{
                    py: 1,
                    borderRadius: 2,
                    textTransform: "none",
                    fontSize: "1rem",
                  }}
                >
                  Detayl Bilgi
                </Button>
              </CardActions>
            </Card>
          </Box>
        ))}
      </Box>
    </Container>
  );
};

export default Courses;
