import React from "react";
import { 
  Box, 
  Container, 
  Typography, 
  Button, 
  Card, 
  CardContent, 
  CardMedia,
  Stack, 
  Fade,
  useTheme
} from "@mui/material";
import { useNavigate } from "react-router-dom";
import LanguageIcon from '@mui/icons-material/Language';
import SchoolIcon from '@mui/icons-material/School';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import classroomImage from "../assets/images/classroom.jpg";
import studentsImage from "../assets/images/students.jpg";
import teacherImage from "../assets/images/teacher.jpg";

const Home = () => {
  const navigate = useNavigate();
  const theme = useTheme();

  return (
    <Box>
      {/* Hero Section */}
      <Box
        sx={{
          backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(${classroomImage})`,
          backgroundSize: "cover",
          backgroundPosition: "center",
          backgroundAttachment: "fixed",
          color: "white",
          py: { xs: 10, md: 20 },
          position: "relative",
        }}
      >
        <Container maxWidth="lg" sx={{ position: "relative", zIndex: 1 }}>
          <Fade in={true} timeout={1000}>
            <Box sx={{ textAlign: "center" }}>
              <Typography
                variant="h2"
                component="h1"
                gutterBottom
                sx={{ 
                  fontWeight: "bold",
                  backgroundImage: `linear-gradient(45deg, ${theme.palette.primary.main} 30%, ${theme.palette.secondary.main} 90%)`,
                  backgroundClip: "text",
                  textFillColor: "transparent",
                  mb: 3,
                  textShadow: "0 2px 10px rgba(0,0,0,0.2)"
                }}
              >
                Yeni Bir Dil, Yeni Bir Dnya
              </Typography>
              <Typography 
                variant="h5" 
                paragraph 
                sx={{ 
                  mb: 4,
                  maxWidth: "800px",
                  mx: "auto",
                  color: theme.palette.text.secondary
                }}
              >
                Profesyonel eitmenlerimizle birlikte dil renme serveninize
                balayn.
              </Typography>
              <Stack 
                direction={{ xs: 'column', sm: 'row' }} 
                spacing={2} 
                justifyContent="center"
                sx={{ mb: 4 }}
              >
                <Button
                  variant="contained"
                  color="primary"
                  size="large"
                  onClick={() => navigate("/courses")}
                  sx={{
                    px: 4,
                    py: 1.8,
                    fontSize: "1.1rem",
                  }}
                >
                  Kurslarmz Kefedin
                </Button>
                <Button
                  variant="outlined"
                  color="secondary"
                  size="large"
                  onClick={() => navigate("/contact")}
                  sx={{
                    px: 4,
                    py: 1.8,
                    fontSize: "1.1rem",
                    borderWidth: 2,
                  }}
                >
                  letiime Gein
                </Button>
              </Stack>
            </Box>
          </Fade>
        </Container>
      </Box>

      {/* Features Section */}
      <Container sx={{ py: 10 }}>
        <Typography 
          variant="h3" 
          component="h2" 
          textAlign="center" 
          sx={{ 
            mb: 6,
            fontWeight: 600,
            backgroundImage: `linear-gradient(45deg, ${theme.palette.primary.main}, ${theme.palette.secondary.main})`,
            backgroundClip: "text",
            textFillColor: "transparent"
          }}
        >
          Neden Bizi Semelisiniz?
        </Typography>
        
        <Stack 
          direction={{ xs: 'column', md: 'row' }} 
          spacing={4} 
          justifyContent="center"
          alignItems="stretch"
        >
          <Fade in={true} timeout={1000} style={{ transitionDelay: '200ms' }}>
            <Card sx={{ display: 'flex', flexDirection: 'column', flex: 1 }}>
              <CardMedia
                component="img"
                image={teacherImage}
                alt="Deneyimli Eitmenler"
                sx={{
                  height: 240,
                  objectFit: "cover",
                }}
              />
              <CardContent sx={{ flexGrow: 1, textAlign: "center" }}>
                <Box sx={{ display: 'flex', justifyContent: 'center', mb: 2 }}>
                  <SchoolIcon color="primary" sx={{ fontSize: 40 }} />
                </Box>
                <Typography variant="h5" gutterBottom sx={{ fontWeight: 500 }}>
                  Deneyimli Eitmenler
                </Typography>
                <Typography variant="body1" color="text.secondary">
                  Anadili olarak konuan, pedagogik formasyona sahip, deneyimli eitmenlerimizle kaliteli dil eitimi aln.
                </Typography>
              </CardContent>
            </Card>
          </Fade>
          
          <Fade in={true} timeout={1000} style={{ transitionDelay: '400ms' }}>
            <Card sx={{ display: 'flex', flexDirection: 'column', flex: 1 }}>
              <CardMedia
                component="img"
                image={studentsImage}
                alt="Esnek Program"
                sx={{
                  height: 240,
                  objectFit: "cover",
                }}
              />
              <CardContent sx={{ flexGrow: 1, textAlign: "center" }}>
                <Box sx={{ display: 'flex', justifyContent: 'center', mb: 2 }}>
                  <AccessTimeIcon color="primary" sx={{ fontSize: 40 }} />
                </Box>
                <Typography variant="h5" gutterBottom sx={{ fontWeight: 500 }}>
                  Esnek Program
                </Typography>
                <Typography variant="body1" color="text.secondary">
                  Size uygun zamanlarda, esnek ders programlar ile eitiminizi aksatmadan dil renin.
                </Typography>
              </CardContent>
            </Card>
          </Fade>
          
          <Fade in={true} timeout={1000} style={{ transitionDelay: '600ms' }}>
            <Card sx={{ display: 'flex', flexDirection: 'column', flex: 1 }}>
              <CardMedia
                component="img"
                image={classroomImage}
                alt="Modern Snflar"
                sx={{
                  height: 240,
                  objectFit: "cover",
                }}
              />
              <CardContent sx={{ flexGrow: 1, textAlign: "center" }}>
                <Box sx={{ display: 'flex', justifyContent: 'center', mb: 2 }}>
                  <LanguageIcon color="primary" sx={{ fontSize: 40 }} />
                </Box>
                <Typography variant="h5" gutterBottom sx={{ fontWeight: 500 }}>
                  Modern Snflar
                </Typography>
                <Typography variant="body1" color="text.secondary">
                  Teknolojik donanml, modern snflarda interaktif yntemlerle eitim aln.
                </Typography>
              </CardContent>
            </Card>
          </Fade>
        </Stack>
      </Container>
      
      {/* CTA Section */}
      <Box
        sx={{
          backgroundColor: 'background.paper',
          py: 10,
          borderTop: `1px solid ${theme.palette.divider}`,
          borderBottom: `1px solid ${theme.palette.divider}`,
        }}
      >
        <Container maxWidth="md">
          <Fade in={true} timeout={1000}>
            <Box textAlign="center">
              <Typography variant="h4" component="h2" gutterBottom sx={{ fontWeight: 600 }}>
                Dil renme Yolculuunuza Bugn Balayn
              </Typography>
              <Typography variant="body1" paragraph sx={{ mb: 4, color: 'text.secondary' }}>
                Hayallerinizi gerekletirmek iin ilk adm atn. Eitmenlerimiz sizinle tanmak iin sabrszlanyor.
              </Typography>
              <Button
                variant="contained"
                color="secondary"
                size="large"
                onClick={() => navigate("/contact")}
                sx={{
                  px: 5,
                  py: 2,
                  fontSize: "1.1rem",
                }}
              >
                cretsiz Tanma Dersi Aln
              </Button>
            </Box>
          </Fade>
        </Container>
      </Box>
    </Box>
  );
};

export default Home;
