import React from "react";
import { Box, Container, Typography } from "@mui/material";
import classroomImage from "../assets/images/IMG_8505.jpg";
import studentsImage from "../assets/images/IMG_8510.jpg";
import teacherImage from "../assets/images/IMG_8507.jpg";

const About = () => {
  return (
    <Container maxWidth="lg" sx={{ py: 8 }}>
      <Box sx={{ mb: 8, textAlign: "center" }}>
        <Box
          component="img"
          src="/heart_of_english_logo.svg"
          alt="Heart of English Logo"
          sx={{
            height: { xs: "80px", md: "120px" },
            mb: 4,
            filter: "drop-shadow(0px 4px 8px rgba(0,0,0,0.1))",
          }}
        />
        <Typography
          variant="h3"
          component="h1"
          gutterBottom
          sx={{ fontWeight: "bold" }}
        >
          Hakkmzda
        </Typography>
        <Typography
          variant="h6"
          color="text.secondary"
          sx={{ maxWidth: 800, mx: "auto" }}
        >
          Heart of English olarak VIP konseptli yabanc dil kursu ve yurtd
          eitim danmanl hizmetleri sunuyoruz.
        </Typography>
      </Box>

      <Box sx={{ mb: 8, display: "flex", flexWrap: "wrap", gap: 6 }}>
        <Box sx={{ flex: { xs: "100%", md: "1" } }}>
          <Box
            component="img"
            src={classroomImage}
            sx={{
              width: "100%",
              height: 400,
              objectFit: "cover",
              borderRadius: 2,
              boxShadow: 3,
            }}
          />
        </Box>
        <Box sx={{ flex: { xs: "100%", md: "1" } }}>
          <Typography variant="h4" gutterBottom sx={{ fontWeight: "bold" }}>
            Eitim Programlarmz
          </Typography>
          <Typography paragraph>
             Uluslararas Snav Hazrlk (TOEFL, IELTS, PTE)
            <br />
             SYM Snav Hazrlk (YDS, YKDL, YKS-DL)
            <br />
             Kids ve Teens Eitimleri
            <br />
             Profesyonel Meslek Gruplarna zel Eitimler
            <br />
             TESOL Sertifika Program
            <br /> Dier Diller (Almanca, Rusa, Franszca, spanyolca)
          </Typography>
        </Box>
      </Box>

      <Box sx={{ textAlign: "center", mb: 8 }}>
        <Typography variant="h4" gutterBottom sx={{ fontWeight: "bold" }}>
          Neden Heart of English?
        </Typography>
        <Box sx={{ mt: 4, display: "flex", flexWrap: "wrap", gap: 4 }}>
          <Box sx={{ flex: { xs: "100%", md: "1" }, textAlign: "center" }}>
            <Box
              component="img"
              src={teacherImage}
              sx={{
                width: "100%",
                height: 200,
                objectFit: "cover",
                borderRadius: 2,
                mb: 2,
              }}
            />
            <Typography variant="h6" gutterBottom>
              Uzman Eitmen Kadrosu
            </Typography>
            <Typography color="text.secondary">
              Hem yabanc hem Trk eitmenlerle kaliteli eitim.
            </Typography>
          </Box>
          <Box sx={{ flex: { xs: "100%", md: "1" }, textAlign: "center" }}>
            <Box
              component="img"
              src={classroomImage}
              sx={{
                width: "100%",
                height: 200,
                objectFit: "cover",
                borderRadius: 2,
                mb: 2,
              }}
            />
            <Typography variant="h6" gutterBottom>
              VIP Eitim Konsepti
            </Typography>
            <Typography color="text.secondary">
              Bireyselletirilmi eitim programlar ve modern snflar.
            </Typography>
          </Box>
          <Box sx={{ flex: { xs: "100%", md: "1" }, textAlign: "center" }}>
            <Box
              component="img"
              src={studentsImage}
              sx={{
                width: "100%",
                height: 200,
                objectFit: "cover",
                borderRadius: 2,
                mb: 2,
              }}
            />
            <Typography variant="h6" gutterBottom>
              Profesyonel Danmanlk
            </Typography>
            <Typography color="text.secondary">
              Yurtd eitim ve kariyer danmanl hizmetleri.
            </Typography>
          </Box>
        </Box>
      </Box>
    </Container>
  );
};

export default About;
